* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #777777;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 50px;
  background-color: white;

  .container {
    background-color: #F7F7F7; }

  .header {
    position: relative;
    height: 95vh;
    // запись в одну строку
    background: linear-gradient(90.05deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url("../image/header-background.jpg") center / cover no-repeat;
    //background-repeat: no-repeat
    //background-size: cover
    //background-position: center
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);

    &__logo {
      position: absolute;
      left: 50px;
      top: 50px;
      height: 70px;
      width: 56px; }

    &__box {
      position: absolute;
      width: 100%;
      text-align: center;
      //выравнивание по центру с помощью абсолютного позиционирования
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .title-first {
        color: #FFFFFF;

        &__main {
          text-transform: uppercase;
          display: block;
          font-weight: 800;
          font-size: 50px;
          line-height: 60px;
          letter-spacing: 10px;
          margin-bottom: 10px;
          animation-name: leftToRight;
          animation-duration: 2s;
          animation-delay: 1s;
          animation-timing-function: ease;
          //свойство для устранения багов с анимацией
          backface-visibility: hidden; }

        &__secondary {
          display: block;
          font-weight: 300;
          font-size: 40px;
          line-height: 48px;
          letter-spacing: 5px;
          animation-name: rightToLeft;
          animation-duration: 2s;
          animation-delay: 1s;
          animation-timing-function: ease;
          backface-visibility: hidden;
          margin-bottom: 65px; } }
      .btn {
        &:link, &:visited {
          text-transform: uppercase;
          text-decoration: none;
          color: #777777;
          padding: 15px 40px;
          background: white;
          border-radius: 50px;
          display: inline-block;
          transition: all .3s; }
        &:hover {
          transform: translateY(-3px);
          box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3); }
        &:active {
          transform: translateY(-2px);
          box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1); } } }



 }    // анимация в css

  @keyframes leftToRight {
    0% {
      opacity: 0;
      transform: translateX(-100px); }
    80% {
      transform: translateX(10px); }

    100% {
      opacity: 1;
      transform: translateX(0px); } }

  @keyframes rightToLeft {
    0% {
      opacity: 0;
      transform: translateX(100px); }
    80% {
      transform: translateX(-10px); }

    100% {
      opacity: 1;
      transform: translateX(0px); } } }
